// -----------------------------------------------------------------------------
// This file contains all styles related to the button light of the site/application.
// For media breaks look in _variables.scss
// -----------------------------------------------------------------------------

// Mobile 320-768//
.btn-light {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  background-color: $color-button-secondary;
  border: 0px;
  border-radius: 20px;
  color: $color-brand-primary;
  font-weight: 400;
  min-height: 24px;
  cursor: pointer;

  padding: 7px 14.5px;
  font-size: 8px;
  line-height: 1.25;

  &--margin-left {
    margin-left: 8px;
  }

  &__icon {
    fill: $color-brand-primary;

    &--margin-left {
      margin-left: 12px;
    }

    &__hero {
        position: absolute;
        z-index: 998;
        left: 69px;
        top: -8.5px;
        width: 160px;
        height: 44px;
        background-color: $color-button-secondary;
        border: 0px;
        border-radius: 20px;
        color: $color-brand-primary;
        font-family: 'DM Sans';
        font-style: normal;
        font-weight: 700;
        font-size: 16px;
        line-height: 1.3;
        text-align: center;
        border-radius: 22px;
        padding: 0;

        &:hover,
        &:active,
        &:focus {
            background-color: $color-button-secondary-hover;
        }
    }

    &--margin-left {
        margin-left: 8px;
    }

    &--hamburger {
        width: 209px;
        height: 44px;
        font-family: 'DM Sans';
        font-style: normal;
        font-weight: 700;
        font-size: 16px;
        line-height: 1.3;
        text-align: center;
        border-radius: 22px;
        padding: 0;
        margin-left: 9%;
        margin-bottom: 19px;
    }

    &:hover,
    &:active,
    &:focus {
        background-color: $color-button-secondary-hover;
    }
  }

  &:hover,
  &:active,
  &:focus {
    background-color: $color-button-secondary-hover;
  }

  &--about {
    padding: 10px 37px;
    font-weight: 700;
    font-size: 14px;
    line-height: 18px;
  }
}

@media screen and (max-width: 767px) {
    .btn-light {

        &__hero {
            display: none;
        }
    }
}

// Tablet 768-1280//
@media screen and (min-width: $tablet) {
  .btn-light {
    &--about {
      margin-top: 50px;
      margin-right: 150px;
      float: left;
      font-size: 16px;
      line-height: 21px;
     }

    &--hamburger {
            margin-left: 10%;
     }
   }

}

// Desktop 1280+//
@media screen and (min-width: $desktop) {
    .btn-light {
        padding: 10px 22px;
        font-size: 14px;
        line-height: 1.285;
        min-height: 40px;

        &--margin-left {
            margin-left: 15px;
        }

        &--hamburger {
            position: relative;
            left: 830px;
            top: -9%;
            width: 159px;
            height: 44px;
            font-weight: 700;
            font-size: 16px;
            line-height: 1.3;
        }

        &__hero {
            display: none;
        }
    

        &--about {
          font-size: 16px;
          line-height: 21px;
        }
    }
}